<template>
  <a-form>
    <a-form-item label="Key" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
      <a-input v-model="privilege.privilegeKey" placeholder="KeyKey"></a-input>
    </a-form-item>
    <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
      <a-input v-model="privilege.name" placeholder=""></a-input>
    </a-form-item>
    <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
      <a-select v-model="privilege.privilegeType" placeholder="">
        <a-select-option v-for="(label, code) in $messages.enums.privilegeType" :key="code" :value="code">{{label}}</a-select-option>
      </a-select>
    </a-form-item>
    <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
      <a-textarea v-model="privilege.description" placeholder=""></a-textarea>
    </a-form-item>
  </a-form>
</template>

<script>
  export default {
    name: "PrivilegeForm",
    computed: {
      privilege() {
        return this.$store.getters.record;
      }
    },
    watch: {
      privilege(val) {
        this.$store.dispatch('setRecord', val);
      }
    }
  }
</script>

