<template>
  <div>
    <a-form>
      <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper" required>
        <a-input v-model="user.username" placeholder="" :disabled="user.id !== undefined"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper">
        <a-input v-model="user.name" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper">
        <a-input v-model="user.phone" placeholder=""></a-input>
      </a-form-item>
      <a-form-item label="Email" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper">
        <a-input v-model="user.email" placeholder="Email"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper">
        <a-input v-model="user.gender" placeholder="Email"></a-input>
      </a-form-item>
      <a-form-item label="" :labelCol="$style.modalForm.label" :wrapperCol="$style.modalForm.wrapper">
        <a-input v-model="user.birthday" placeholder="Email"></a-input>
      </a-form-item>
    </a-form>
  </div>
</template>

<script>
  export default {
    name: "UserForm",
    computed: {
      user() {
        return this.$store.getters.record;
      }
    },
    watch: {
      user(val) {
        this.$store.dispatch('setRecord', val);
      }
    }
  }
</script>

